@charset "UTF-8";
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  height: auto;
}

input,
button,
textarea,
select {
  font-family: inherit;
}

button {
  border: none;
  cursor: pointer;
}

textarea {
  resize: vertical;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background: #333;
  font-family: sans-serif;
  color: #fff;
}

@keyframes falling-ball {
  0% {
    bottom: 150px;
  }
  10% {
    bottom: 100px;
  }
  20% {
    bottom: 150px;
  }
  30% {
    bottom: 100px;
  }
  40% {
    bottom: 140px;
  }
  50% {
    bottom: 100px;
  }
  60% {
    bottom: 130px;
  }
  70% {
    bottom: 100px;
  }
  80% {
    bottom: 120px;
  }
  90% {
    bottom: 100px;
  }
  100% {
    bottom: 110px;
  }
}
html {
  scroll-padding-top: 46px;
}

body {
  font-family: "Montserrat", sans-serif;
}

.section-title {
  position: relative;
  padding: 40px;
  text-align: center;
  text-transform: uppercase;
  color: #000;
  font-size: 2.8rem;
}

.section-title::after {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%);
  width: 50px;
  height: 5px;
  background-color: #00adb5;
  content: "";
}

.navbar {
  text-transform: uppercase;
  background-color: rgb(57, 62, 70);
  width: 100vw;
  z-index: 10;
  transition: background-color 0.3s;
}
.navbar .navbar-btn {
  padding: 10px;
  font-size: 1.6rem;
  border: none;
  color: #fff;
  transition: color 0.3s;
}
.navbar .navbar-btn:hover {
  color: #00adb5;
}
.navbar .navbar-theme {
  color: #fff;
  font-size: 1.4rem;
  font-weight: bold;
}
.navbar .navbar-theme .fas {
  margin-right: 10px;
  transition: color 0.3s;
}
.navbar .navbar-theme span {
  transition: color 0.3s;
}
.navbar .navbar-theme:hover .fas,
.navbar .navbar-theme:hover span {
  color: #00adb5;
}
.navbar .navbar-items {
  text-align: center;
}
.navbar .navbar-items .nav-link {
  padding: 10px;
  color: #fff;
  font-size: 1.6rem;
  font-weight: bold;
  transition: color 0.3s;
}
.navbar .navbar-items .nav-link:hover {
  color: #00adb5;
}

.header {
  height: 100vh;
}
.header .hero-img {
  position: relative;
  height: 100vh;
  background-image: url(../img/small.jpg);
  background-size: cover;
  z-index: 5;
}
.header .hero-img .hero-shadow {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  z-index: -100;
}
.header .hero-img .hero-text {
  display: flex;
  height: 100%;
  padding: 20px;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  z-index: 5;
}
.header .hero-img .hero-text .header-title {
  margin-bottom: 10px;
  letter-spacing: 2px;
  font-size: 3.2rem;
  text-transform: uppercase;
}
.header .hero-img .hero-text .header-info {
  font-size: 1.6rem;
}

.about-us {
  background-color: #fff;
}
.about-us .about-us-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.about-us .about-us-items .item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}
.about-us .about-us-items .item .item-title {
  margin-bottom: 20px;
  color: #000;
  font-size: 2rem;
  text-transform: uppercase;
  text-align: center;
}
.about-us .about-us-items .item .bg-item {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
}
.about-us .about-us-items .item .bg-item .text-item {
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  border: 1px solid black;
  border-radius: 50%;
  height: 250px;
  width: 250px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
  transition: opacity 0.5s;
}
.about-us .about-us-items .item .bg-item .text-item .item-info {
  color: #fff;
  font-size: 1.2rem;
}
.about-us .about-us-items .item .bg-item .text-item:hover {
  opacity: 1;
}
.about-us .about-us-items .item .bg-item-history {
  background-image: url("../img/history.jpg");
}
.about-us .about-us-items .item .bg-item-localization {
  background-image: url("../img/localization.jpg");
}
.about-us .about-us-items .item .bg-item-quality {
  background-image: url("../img/quality.jpg");
  background-position: left;
}
.about-us .services {
  text-align: center;
  height: 100px;
  background-color: rgb(119, 119, 119);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  font-size: 1.2rem;
}
.about-us .services .fas {
  margin: 10px;
  transition: transform 0.3s;
}
.about-us .services .fas:hover {
  transform: scale(1.05);
}
.about-us .services p {
  margin-top: 10px;
  color: #fff;
}

.offer {
  background-color: #fff;
}
.offer .offers-box {
  display: flex;
  flex-wrap: wrap;
}
.offer .offers-box .offer {
  position: relative;
  width: 100vw;
  height: 300px;
  background-size: cover;
  transition: transform 0.3s;
}
.offer .offers-box .offer .offer-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-15deg);
  font-size: 4.8rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
}
.offer .offers-box .offer .bg-shadow {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.offer .offers-box .offer .offer-btn {
  position: absolute;
  left: 50%;
  bottom: 30px;
  width: 140px;
  height: 50px;
  border: 3px solid white;
  border-radius: 10px;
  color: rgb(255, 255, 255);
  font-size: 1.6rem;
  transform: translate(-50%);
  background-color: rgba(255, 255, 255, 0.3);
  z-index: 5;
  font-weight: bold;
  text-transform: uppercase;
  padding: 10px 20px;
  cursor: pointer;
  transition: transform 0.3s, background-color, 0.3s;
}
.offer .offers-box .offer .offer-btn:hover {
  transform: translate(-50%) scale(1.1);
  background-color: rgba(0, 0, 0, 0.3);
  color: #fff;
}
.offer .offers-box .one {
  background-image: url(../img/rakiety_small.jpg);
}
.offer .offers-box .two {
  background-image: url(../img/buty_small.jpg);
}
.offer .offers-box .three {
  background-image: url(../img/strój_small.jpg);
  background-position: center;
}
.offer .offers-box .four {
  background-image: url(../img/akcesoria_small.jpg);
}

.brands {
  background-color: #fff;
  padding-bottom: 20px;
}
.brands .brands-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.brands .brands-box .brand {
  margin: 30px;
}
.brands .brands-box .brand img {
  width: 120px;
  transition: transform 0.3s;
}
.brands .brands-box .brand img:hover {
  transform: scale(1.1);
}

.contact {
  background-color: #fff;
  color: #000;
}
.contact .contact-form {
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  padding: 30px 40px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact .contact-form .label-title {
  margin-bottom: 5px;
  font-size: 1.4rem;
}
.contact .contact-form input,
.contact .contact-form textarea {
  margin-bottom: 20px;
  width: 100%;
  padding: 5px;
  font-size: 1.2rem;
}
.contact .contact-form textarea {
  min-height: 200px;
  max-height: 200px;
}
.contact .contact-form .form-btn {
  padding: 5px 10px;
  width: 100px;
  background: none;
  border: 1px solid black;
  border-radius: 5px;
  text-transform: uppercase;
  font-size: 1.4rem;
  transition: background-color 0.3s;
}
.contact .contact-form .form-btn:hover {
  background-color: #fff;
}
.contact .social-media {
  position: relative;
  width: 100%;
  height: 200px;
  background-image: url(../img/contact_small.jpg);
  background-size: cover;
  background-position: center;
  z-index: 1;
}
.contact .social-media .social-media-items {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px;
  z-index: 1;
  color: #fff;
}
.contact .social-media .social-media-items .text {
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.4rem;
}
.contact .social-media .social-media-items .icons-box {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
}
.contact .social-media .social-media-items .icons-box .fab {
  padding: 10px;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  transition: transform 0.3s;
}
.contact .social-media .social-media-items .icons-box .fab:hover {
  transform: scale(1.2);
}
.contact .social-media .bg-shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -10;
}

.footer .footer-area {
  height: 40px;
  background-color: #eeeeee;
  text-align: center;
  line-height: 40px;
  font-size: 1.8rem;
  color: #000;
}

@media (min-width: 576px) {
  .header .hero-img .hero-text .header-title {
    font-size: 3.8rem;
  }
  .header .hero-img .hero-text .header-info {
    font-size: 2rem;
  }
  .about-us .about-us-items .item .item-title {
    font-size: 2.4rem;
  }
  .about-us .about-us-items .item .bg-item {
    width: 300px;
    height: 300px;
  }
  .about-us .about-us-items .item .bg-item .text-item {
    height: 300px;
    width: 300px;
  }
  .about-us .about-us-items .item .bg-item .text-item .item-info {
    font-size: 1.4rem;
  }
  .brands .brands-box {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .brands .brands-box .brand img {
    width: 120px;
  }
  .contact .contact-form .label-title {
    font-size: 1.6rem;
  }
  .contact .social-media .social-media-items .text {
    font-size: 1.6rem;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .navbar .navbar-theme {
    font-size: 1.6rem;
  }
  .about-us .about-us-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .about-us .about-us-items .item {
    margin: 0 20px 40px;
  }
  .about-us .about-us-items .item .item-title {
    font-size: 2.4rem;
  }
  .about-us .about-us-items .item .bg-item {
    width: 300px;
    height: 300px;
  }
  .about-us .about-us-items .item .bg-item .text-item {
    height: 300px;
    width: 300px;
  }
  .about-us .services {
    font-size: 1.6rem;
  }
  .about-us .services .fas {
    margin: 20px;
  }
}
@media (min-width: 576px) and (max-width: 992px) {
  .navbar .navbar-items {
    padding: 10px;
    border-top: 2px solid white;
  }
}
@media (min-width: 576px) and (min-width: 992px) {
  .navbar {
    height: 40px;
  }
  .navbar .navbar-theme {
    font-size: 1.8rem;
  }
  .navbar .navbar-items .nav-link {
    margin: 0 10px;
  }
  .navbar .navbar-items .active {
    color: #537fe7;
  }
  .header .hero-img {
    background-image: url(../img/big.jpg);
  }
  .header .hero-img .hero-text .header-title {
    font-size: 4.2rem;
  }
  .header .hero-img .hero-text .header-info {
    font-size: 2.4rem;
  }
  .header .hero-img .ball-icon {
    position: absolute;
    width: 50px;
    height: 50px;
    bottom: 200px;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background-image: url("https://cdn.pixabay.com/photo/2013/07/12/12/45/tennis-ball-146167_960_720.png");
    z-index: 5;
    background-size: cover;
    animation: falling-ball 3s infinite;
  }
  .about-us .about-us-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .about-us .about-us-items .item {
    margin: 0 60px 40px;
  }
  .about-us .about-us-items .item .item-title {
    font-size: 2.4rem;
  }
  .about-us .about-us-items .item .bg-item {
    width: 300px;
    height: 300px;
  }
  .about-us .about-us-items .item .bg-item .text-item {
    height: 300px;
    width: 300px;
  }
  .offer {
    background-color: #fff;
  }
  .offer .offers-box {
    align-items: center;
    justify-content: center;
    gap: 3px;
  }
  .offer .offers-box .offer {
    width: 48vw;
    height: 400px;
  }
  .offer .offers-box .offer .offer-title {
    font-size: 5.8rem;
  }
  .offer .offers-box .offer:hover {
    transform: scale(0.98);
  }
  .contact .contact-form .label-title {
    font-size: 1.8rem;
  }
  .contact .contact-form input,
  .contact .contact-form textarea {
    font-size: 1.4rem;
  }
  .contact .contact-form .form-btn {
    padding: 6px 12px;
    font-size: 1.6rem;
  }
  .contact .social-media {
    height: 150px;
    background-image: url(../img/contact_big.jpg);
  }
  .contact .social-media .social-media-items .text {
    margin-bottom: 20px;
    text-align: center;
    font-size: 1.8rem;
  }
  .contact .social-media .social-media-items .icons-box {
    font-size: 3rem;
  }
  .contact .social-media .social-media-items .icons-box .fab {
    margin-right: 30px;
  }
}
@media (min-width: 576px) and (min-width: 1200px) {
  .about-us .about-us-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .about-us .about-us-items .item {
    margin: 0 25px 40px;
  }
  .about-us .about-us-items .item .item-title {
    font-size: 2.4rem;
  }
  .about-us .about-us-items .item .bg-item {
    width: 300px;
    height: 300px;
  }
  .about-us .about-us-items .item .bg-item .text-item {
    height: 300px;
    width: 300px;
  }
  .about-us .services {
    font-size: 1.8rem;
  }
  .about-us .services .fas {
    margin: 30px;
  }
}